import React, { useState } from 'react';

import AlgoLayout from '../components/shared/AlgoLayout/AlgoLayout';
import sortingAlgorithms from '../algo-data/sorting/sortingAlgorithmsInformaion';
import Modal from '../components/shared/Modal/Modal';

const SortingAlgos = () => {
    const [firstVisit, setFirstVisit] = useState(true);

    return (
        <div>
            {firstVisit && <Modal show={true} onCancel={() => setFirstVisit(false)} />}
            <AlgoLayout algosType={'sorting'} algos={sortingAlgorithms} />
        </div>
    );
};

export default SortingAlgos;
