import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import { anime } from 'react-anime';

import Backdrop from './Backdrop';
import './Modal.css';

const ModalOverlay = (props) => {
    useEffect(() => {
        const animation = () => {
            const timeline = anime.timeline();
            timeline
                .add({
                    targets: '.title',
                    opacity: [0, 1],
                    translateY: [500, 0],
                    translateX: [70, 0],
                    scale: [10, 1],
                    duration: 500,
                    easing: 'easeInQuad',
                })
                .add(
                    {
                        targets: '.left',
                        opacity: [0, 1],
                        translateX: [-100, 0],
                        duration: 500,
                        easing: 'linear',
                    },
                    '-=200'
                )
                .add(
                    {
                        targets: '.right',
                        opacity: [0, 1],
                        translateX: [100, 0],
                        duration: 500,
                        easing: 'linear',
                    },
                    '-=500'
                )
                .add(
                    {
                        targets: '.img',
                        opacity: [0, 1],
                        scale: [0.5, 1],
                        duration: 500,
                        easing: 'linear',
                    },
                    '-=500'
                );
        };
        animation();
    }, []);

    const content = (
        <div className="modal-wrapper">
            <div className={`modal`}>
                <header className={`modal__header`}>
                    <h1 className="title">Welcome to my Algorithm Visualization Project!</h1>
                    <img src={process.env.PUBLIC_URL + '/picture_of_me.jpg'} className="img" alt="Timo Diehm" />
                </header>
                <form onSubmit={(event) => event.preventDefault()}>
                    <div className={`modal__content`}>
                        <div className="left">
                            <h3>What is a sorting algorithm anyway?</h3>
                            <p>
                                A sorting algorithm is used to arrange data in an ascending or descending order. Being
                                sorted, the data is much more useful to work with. An easy example would be to sort
                                emails by sent date so they show up properly. In Computer Science there isn't such an
                                easy solution as it could been thought by people, who aren't into it. The time it takes
                                to sort data can quickly increase exponentially to it's length. Especially nowadays, in
                                the age of Big Data, sorting efficiently is enormously important. Google, for instance,
                                first rates websites matching your search criteria and afterwards sort them in order of
                                their rating. This should obviously happen as fast as possible.
                            </p>
                        </div>
                        <div className="right">
                            <div>
                                <h3>How are they demonstrated in this project?</h3>
                                <p>
                                    In this project I use sticks with a specific height representing a specific number.
                                    At first, they are randomly arranged. By pressing the start button the currently
                                    selected algorithm then sorts those sticks. You can also adjust speed and amount of
                                    sticks by either selecting a preset or by customly changing them. For each
                                    algorithm, there is a brief introduction, a longer description and the semi original
                                    code I used. Being quite complex with all the animation code within the original
                                    algorithm code, I shortened it by the specific visualization code.
                                </p>
                            </div>
                        </div>
                    </div>
                    <footer className={`modal__footer ${props.footerClass}`}>
                        <button onClick={() => props.onCancel()}>GOT IT!</button>
                    </footer>
                </form>
            </div>
        </div>
    );
    return ReactDOM.createPortal(content, document.getElementById('modal-hook'));
};

const Modal = (props) => {
    return (
        <React.Fragment>
            {props.show && <Backdrop onClick={props.onCancel} />}
            <ModalOverlay {...props} />
        </React.Fragment>
    );
};

export default Modal;
